  list-style: none;
  text-align: center;
  width: 70vw;
  justify-content: end;
  margin-right: 2rem;
}

.nav-links {
  color: white;
  text-decoration: none;
  padding: 0.5rem 1rem;
}

.nav-links:hover {
  background-color: #44d6f0;
  border-radius: 4px;
  transition: all 0.2s ease-out;
}

.fa-bars {
  color: #FFF;
}

.nav-links-mobile {
  display: none;
