 
.about {
    width: 100%;
    background: #23263a;
    padding: 100px 0;
  }
  .about__img {
    padding: 30px;
  }
  .about__img img {
    width: 300px;
    height: 400px;
  }
  .about__info {
    color: #fff;
  }
  .about__info h1 {
    margin-bottom: 30px;
  }
  .about__info-p1 {
    font-size: 16px;
    margin-bottom: 20px;
    text-align: justify;
  }
  .about__info-p2 {
    font-size: 16px;
    text-align: justify;
  }
  
  .info__contacts {
    margin-top: 20px;
  }
  .info__contacts strong {
    color: #ff4a57;
  }
  .info__contacts p {
    margin-top: 3px;
  } 
   .info__contacts .col-6 {
    margin: 10px 0;
  }
  .container {
    width: 1200px;
    margin: 0 auto;
  }
  .mainHeader {
    font-size: 32px;
    margin-bottom: 20px;
    color: #fff;
  }
  .mainContent {
    font-size: 16px;
    color: #dfdfdf;
  }
  .common {
    width: 33.33333333333333%;
    margin: 20px auto;
    text-align: center;
  }
  .commonBorder {
    background: #ff4a57;
    height: 3px;
    width: 100px;
    margin: 30px auto;
  }
  .alignCenter {
    align-items: center;
  }
  .justifyConter {
    justify-content: center;
  }
  
.h-650 {
    height: 650px;
  }
  .row {
    display: flex;
    width: 100%;
    flex-wrap: wrap;
  }
  .col-6 {
    width: 50%;
  }

  
@media (max-width: 765px) {
    .container {
      width: 100%;
      padding: 0 50px;
    }

    .common {
      width: 100%;
    }
    .h-650 {
      height: auto;
    }
  }
  
  