:root{
  --header-height: 3rem;
  --font-semi: 600;
}

/*===== Colores =====*/
:root{
  --first-color: #4070F4;
  --second-color: #0E2431;
}

/*===== Fuente y tipografia =====*/
:root{
  --body-font: 'Poppins', sans-serif;
  --big-font-size: 2rem;
  --h2-font-size: 1.25rem;
  --normal-font-size: 0.938rem;
}
@media screen and (min-width: 768px){
  :root{
    --big-font-size: 3.5rem;
    --h2-font-size: 2rem;
    --normal-font-size: 1rem;
  }
}

/*===== Margenes =====*/
:root{
  --mb-1: 0.5rem;
  --mb-3: 1.5rem;
  --mb-5: 2.5rem;
  --mb-6: 3rem;
}

/*===== z index =====*/
:root{
  --z-back: -10;
  --z-normal: 1;
  --z-tooltip: 10;
  --z-fixed: 100;
}


.skills__container{
    margin-right: auto;
    margin-left: auto;
    row-gap: 2rem;

  }
  .skills__subtitle{
    font-size: 1.5rem;
    font-weight: 800;
    text-align: left;
    margin-bottom: 1rem;
  }
  .skills__text{
    margin-bottom: 1rem;
  }
  .skills__data{
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
    font-weight: 600;
    padding: .5rem 1rem;
    margin-bottom: 2rem;
    border-radius: .5rem;
    box-shadow: 0 4px 25px rgba(14,36,49,.15);
  }
  .skills__icon{
    font-size: 2rem;
    margin-right:1rem;
    color: #4070F4;
  }
  .skills__names{
    display: flex;
    align-items: center;
  }
  .skills__bar{
    position: absolute;
    left: 0;
    bottom: 0;
    background-color: #4070F4;
    height: .25rem;
    border-radius: .5rem;
    z-index: -10;
  }
  .skills__html{
    width: 95%;
  }
  .skills__css{
    width: 85%;
  }
  .skills__js{
    width: 65%;
  }
  .skills__ux{
    width: 85%;
  }
  .skills__img{
    border-radius: .5rem;
  }


  .section-title{
  position: relative;
  font-size: -1.25rem;
  color: #4070F4;
  margin-top: 1rem;
  margin-bottom: 2rem;
  text-align: center;
}
.section-title::after{
  position: absolute;
  content: "";
  width: 64px;
  height: 0.18rem;
  left: 0;
  right: 0;
  margin: auto;
  top: 2rem;
  background-color: #4070F4;
}
.section{
  padding-top: 3rem;
  padding-bottom: 2rem;
}

.bd-grid{
  width: calc(100% - 2rem);
  margin-left: 1rem;
  margin-right: 1rem;
}

.dot-1 {
  
  position: relative;
  height: 12vw;
  background-color:blueviolet;
  border-radius: 50%;
  display: inline-block;
}
.dot-2 {
  position: relative;
  height: 12vw;
  background-color: red;
  border-radius: 50%;
  display: inline-block;
}
.circular--portrait {
  width: 20vw;
  height: 20vw;
  overflow: hidden;
  border-radius: 50%;
}
.lang{
  width: 50em;
  padding: 5rem;
}


ul, li { list-style: none; margin: 0; padding: 0; }

ul { display: -webkit-flex; display: flex; text-align: center; justify-content: space-between; margin-bottom: 30px; }
.a1 { width: 35%; text-align: center; position: relative; padding-bottom: 35%; border-radius: 50%; overflow: hidden; background-color: #ff0082; }
.a2 { width: 35%; text-align: center; position: relative; padding-bottom: 35%; border-radius: 50%; overflow: hidden; background-color: #00ff0dbe; }

ul li h3 { position: absolute; top: 50%; left: 50%; transform: translate(-50%,-50%); color: #fff; margin: 0; padding: 0; font-weight: 500; }

.percentage{
  width: 20rem;
  height: 20rem;
  margin-left: auto;
  margin-right: auto;
}
.tt { 
  position: relative;
  top: 8em; 
  left: -5em; 
  line-height: 110%;
  color: #000; }
  